import React, { useState, useEffect } from "react";
import type { BoxProps, ComponentDefaultProps, HeadingProps, TextProps } from "@chakra-ui/react";
import { Box, Heading, Text, useColorMode, Tooltip, IconButton } from "@chakra-ui/react";
import { Prism as SyntaxHighlighter } from "react-syntax-highlighter";
import { oneLight, oneDark } from "react-syntax-highlighter/dist/cjs/styles/prism";
import { FiCopy, FiCheck } from "react-icons/fi";
import { MDXProvider } from "@mdx-js/react";
import type { AnchorHTMLAttributes, PropsWithChildren } from "react";
import Callout from "./Callout";
import { InternalLink } from "./InternalLink";
import YouTubePlayer from "./YouTubePlayer";

const CopyButton = ({ text }: { text: string }) => {
  const [copied, setCopied] = useState(false);
  const { colorMode } = useColorMode();

  const handleCopy = async () => {
    try {
      await navigator.clipboard.writeText(text);
      setCopied(true);
      setTimeout(() => setCopied(false), 2000); // 2    
    } catch (error) {
      console.error("Failed to copy text:", error);
    }
  };

  return (
    <Tooltip label={copied ? "Copied!" : "Copy"} hasArrow placement="top">
      <IconButton
        icon={copied ? <FiCheck /> : <FiCopy />}
        onClick={handleCopy}
        size="md"
        position="absolute"
        top="12px"
        right="8px"
        aria-label="Copy code"
        backgroundColor={colorMode === "dark" ? "gray.800" : "gray.200"} //  
        color={colorMode === "dark" ? "white" : "gray.900"} //  
        _hover={{
          backgroundColor: colorMode === "dark" ? "gray.600" : "gray.400", //   
        }}
        _active={{
          backgroundColor: colorMode === "dark" ? "gray.700" : "gray.500", //   
        }}
        borderRadius="10px"
      />
    </Tooltip>
  );
};

const InlineCode = ({ children }: { children: string }) => {
  const { colorMode } = useColorMode();
  const theme = colorMode === "dark" ? oneDark : oneLight;

  const inlineStyle = {
    backgroundColor: colorMode === "dark" ? "gray.900" : "gray.50", //    
    color: colorMode === "dark" ? "white" : "gray.900",
    padding: "2px 6px",
    borderRadius: "4px",
    fontWeight: "550",
    textDecoration: "underline", //  
    textDecorationColor: colorMode === "dark" ? "blue.400" : "blue.500", //   
    textDecorationThickness: "2px", //   
    textUnderlineOffset: "5px", //    
  };

  return (
    <Text as="code" sx={inlineStyle}>
      {children}
    </Text>
  );
};

const CodeBlock = (props: any) => {
  const { className, children } = props;
  const match = /language-(\w+)/.exec(className || "");
  const { colorMode } = useColorMode();
  const theme = colorMode === "dark" ? oneDark : oneLight;
  const language = match ? match[1] : "text"; //     "text"
  const [fontSize, setFontSize] = useState(14);

  useEffect(() => {
    const updateFontSize = () => {
      const width = window.innerWidth;

      if (width < 768) {
        setFontSize(13);
      } else if (width < 1024) {
        setFontSize(14);
      } else {
        setFontSize(15);
      }
    };

    updateFontSize();
    window.addEventListener("resize", updateFontSize);

    return () => {
      window.removeEventListener("resize", updateFontSize);
    };
  }, []);

  if (!match) {
    //    InlineCode  
    return <InlineCode>{children}</InlineCode>;
  }

  return (
    <Box
      position="relative"
      marginTop="10px"
      borderRadius="10px"
      overflow="hidden"
      mb="20px"
      boxShadow={
        colorMode === "dark"
          ? "0px 4px 12px rgba(0, 0, 0, 0.6)"
          : "0px 4px 12px rgba(0, 0, 0, 0.2)"
      }
    >
      {/*   */}
      <Box
        bg={theme['code[class*="language-"]']?.background || "inherit"}
        height="40px"
        px="10px"
        display="flex"
        alignItems="center"
        justifyContent="space-between" //   
        borderBottom={`1px solid ${colorMode === "dark" ? "gray.700" : "gray.400"}`}
        paddingLeft="20px"
        paddingTop="25px"
        paddingBottom="25px"
      >
        {/* Mac  +   */}
        <Box display="flex" alignItems="baseline">
          {/* Mac  */}
          <Box display="flex" gap="8px">
            <Box width="12px" height="12px" borderRadius="full" bg="red.500" />
            <Box width="12px" height="12px" borderRadius="full" bg="yellow.500" />
            <Box width="12px" height="12px" borderRadius="full" bg="green.500" />
          </Box>

          {/*   (Mac  ) */}
          <Box
            fontSize="14px"
            fontWeight="bold"
            textTransform="uppercase"
            color={colorMode === "dark" ? "whiteAlpha.800" : "gray.700"}
            padding="2px 12px"
            borderRadius="5px"
            textAlign="left"
          >
            {language}
          </Box>
        </Box>

        {/*   */}
        <CopyButton text={String(children).replace(/\n$/, "")} />
      </Box>

      {/*   */}
      <Box>
        <SyntaxHighlighter
          style={theme}
          customStyle={{
            margin: "0px",
            borderRadius: "0px 0px 10px 10px",
            fontFamily: "Fira Code, monospace",
            fontSize: `${fontSize}px`,
          }}
          showLineNumbers
          lineNumberStyle={{
            width: "42px",
            textAlign: "right",
            paddingRight: "18px",
            fontSize: `${fontSize - 2}px`,
          }}
          PreTag="div"
          language={language}
          {...props}
        >
          {String(children).replace(/\n$/, "")}
        </SyntaxHighlighter>
      </Box>
    </Box>
  );
};

const customComponents = {
  h1: (props: HeadingProps) => <Heading as="h1" fontSize={36} mt="80px" {...props} />,
  h2: (props: HeadingProps) => <Heading as="h2" fontSize={32} mt="60px" mb="30px" {...props} />,
  h3: (props: HeadingProps) => <Heading as="h3" fontSize={24} mt="60px" mb="20px" {...props} />,
  h4: (props: HeadingProps) => <Heading as="h4" fontSize={20} mt="40px" mb="20px" {...props} />,
  p: (props: TextProps) => <Text fontSize={16} mt="20px" lineHeight="1.8" {...props} />,
  li: (props: BoxProps) => (
    <Box
      as="li"
      sx={{
        wordBreak: "break-word", //     
        marginLeft: "24px",
        lineHeight: "1.8",
      }}
      m={"4px 0"}
      fontSize={16}
      {...props}
    />
  ),
  ol: (props: BoxProps) => (
    <Box
      as="ol"
      sx={{
        listStylePosition: "inside", //    
        listStyleType: "decimal", //   
        textIndent: "-1.2em", //    
        "* > ol": {
          margin: 0,
        },
      }}
      {...props}
    />
  ),
  ul: (props: BoxProps) => (
    <Box
      as="ul"
      sx={{
        listStylePosition: "inside", //    
        listStyleType: "disc", //  bullet 
        // marginLeft: "0px",
        textIndent: "-1.2em", //    
        "* > ul": {
          margin: 0,
          // padding: 0,
        },
      }}
      {...props}
    />
  ),
  a: (props: AnchorHTMLAttributes<HTMLAnchorElement>) => {
    const ariaHidden = props["aria-hidden"];
    const isInternalLink = props.href?.startsWith("/");
    const href = props.href;

    if (!href) return <a {...props} />;

    if (isInternalLink) {
      return <InternalLink to={href}>{props.children}</InternalLink>;
    }

    return (
      <Box
        as="span"
        _hover={{
          textDecoration: "underline",
        }}
      >
        <a
          style={{
            fontWeight: 600,
            color: "var(--chakra-colors-blue-400)",
          }}
          target={!ariaHidden ? "_blank" : undefined}
          {...props}
        />
      </Box>
    );
  },
  blockquote: (props: ComponentDefaultProps) => {
    const children = props.children;
    return <Callout>{children}</Callout>;
  },
  code: CodeBlock,
  Callout,
  YouTubePlayer,
};

export default function ({ children }: PropsWithChildren) {
  return <MDXProvider components={customComponents as any}>{children}</MDXProvider>;
}