import { Box, IconButton } from "@chakra-ui/react";
import { ArrowUpIcon } from "@chakra-ui/icons"; // Chakra UI  
import React from "react";

import Footer from "./Footer";
import Header from "./header/Header";
import MDXProvider from "./mdx/MDXProvider";
import TableOfContents from "./TableOfContents";

interface LayoutProps {
  children: React.ReactNode;
  tableOfContents?: any;
}

export default function PostLayout({ children, tableOfContents }: LayoutProps) {
  // "  "     
  const scrollToTop = () => {
    const scrollHeight = window.scrollY;
    const scrollSpeed = 25; //    (    )
    const scrollStep = Math.ceil(scrollHeight / scrollSpeed);

    const scrollInterval = setInterval(() => {
      if (window.scrollY <= 0) {
        clearInterval(scrollInterval);
      } else {
        window.scrollBy(0, -scrollStep); //  
      }
    }, 10);
  };

  return (
    <MDXProvider>
      <Header />

      {/* Article    */}
      <Box
        as="main"
        position="relative" // TOC article    relative 
        margin="50px auto"
        padding="20px"
        maxWidth={{ base: "750px", "1.75xl": "800px", "4xl": "880px" }}
        wordBreak="keep-all"
        overflowWrap="break-word"
        lineHeight="1.7"
        letterSpacing="-0.04px"
      >
        {children}
      </Box>

      {/* TOC article   100px sticky  */}
      {tableOfContents && (
        <Box
          as="nav"
          width="300px"
          position="fixed"
          top="150px"
          right={{ base: "calc(50% - 400px - 300px)", "1.75xl": "calc(50% - 400px - 400px)"}} // article  100px 
          height="fit-content"
          display={{ base: "none", xl: "block" }}
        >
          <TableOfContents tableOfContents={tableOfContents} />
        </Box>
      )}

      {/*     */}
      <IconButton
        icon={<ArrowUpIcon />}
        aria-label="Go to top"
        position="fixed"
        bottom={{ base: "12px", sm: "15px", md: "30px" }}
        right={{ base: "12px", sm: "15px", md: "30px" }}
        size="md"
        borderRadius="full"
        boxShadow="0 4px 6px rgba(0, 0, 0, 0.2)"
        onClick={scrollToTop}
        zIndex="999"
        _light={{
          backgroundColor: "transparent",  //    
        }}
        _focus={{
          backgroundColor: "transparent",  //    
        }}
        _dark={{
          boxShadow: "0 4px 6px rgba(0, 0, 0, 0.3)",
        }}
        transition="background-color 0.3s ease"  //    
      />

      <Footer />
    </MDXProvider>
  );
}